.main-nav {
  position: relative;
  width: 100%;
  min-height: 51px;
}

.main-nav--closed {
  @include below ($tablet-width) {
    .main-nav__toggle::before {
      content: "";
      position: absolute;
      top: 21px;
      left: 15px;
      width: 22px;
      height: 1px;
      background-color: $basic-black;
      box-shadow: 0 5px 0 0 $basic-black, 0 10px 0 0 $basic-black;
      }

    .main-nav__wrapper {
      display: none;
    }
   }
}

.main-nav--opened {
  @include below ($tablet-width) {
    .main-nav__toggle{
      background-color: initial;

      &::before,
      &::after {
        content: "";
        position: absolute;
        top: 25px;
        left: 15px;
        width: 22px;
        height: 1px;
        background-color: $basic-black-20;
      }

      &::before {
        transform: rotate(45deg);
        box-shadow: none;
      }

      &::after {
        transform: rotate(-45deg);
      }

      &:active::before,
      &:active::after {
        background-color: fade($basic-black-20, 30%);
      }
    }

    .main-nav__wrapper {
      position: absolute;
      left: 50%;
      transform: translateX(-55%);
      width: 100vw;
      z-index: 1;
      background-color: $basic-white;
    }
   }
  }


.main-nav__toggle {
  display: none;

  @include below ($tablet-width) {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    display: block;
    width: 50px;
    height: 50px;
    background-color: $accent-color;
    cursor: pointer;
    border: none;

    &:hover,
    &:active {
      background-color: fade($accent-color, 10%);
    }

}
}

 .main-nav--nojs {
   &.main-nav__toggle {
      display: none;
   }

   &.site-list__item {
    display: block;
  }
 }


.main-nav__list{
  @include list-reset;
}
